@import "../../6.UTILIDADES/mixins/font/param.personalized-font";
@import "../../6.UTILIDADES/variables";


.divModalContainer {
	position: fixed;
	font-family: Arial, Helvetica, sans-serif;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background: rgba(0,0,0,0.8);
	z-index: 99999;
	opacity:0;
	-webkit-transition: opacity 400ms ease-in;
	-moz-transition: opacity 400ms ease-in;
	transition: opacity 400ms ease-in;
	pointer-events: none;


	&__divModalDialog {
		display: flex;
		flex-direction: column;
		width: 400px;
		position: relative;
		margin: 10% auto;
		padding: 5px 20px 13px 20px;
		border-radius: 10px;
		background: #fff;
		background: -moz-linear-gradient(#fff, #999);
		background: -webkit-linear-gradient(#fff, #999);
		background: -o-linear-gradient(#fff, #999);
		-webkit-transition: opacity 400ms ease-in;
		-moz-transition: opacity 400ms ease-in;
		transition: opacity 400ms ease-in;
	
		&__a {
			background: #606061;
			color: #FFFFFF;
			line-height: 25px;
			position: absolute;
			right: -12px;
			text-align: center;
			top: -10px;
			width: 24px;
			text-decoration: none;
			font-weight: bold;
			-webkit-border-radius: 12px;
			-moz-border-radius: 12px;
			border-radius: 12px;
			-moz-box-shadow: 1px 1px 3px #000;
			-webkit-box-shadow: 1px 1px 3px #000;
			box-shadow: 1px 1px 3px #000;
		}

		&__img{
			padding-bottom: 1rem;
		}

		&__h{
			padding: 1rem 0;
			@include letra-personalizada(left, 1.3rem, $divEncabezado1, 3px, none, $tipografia-roboto, uppercase, 600);
		}

		&__p{
			@include letra-personalizada(center, 1rem, $divEncabezado1, 1px, none, $tipografia-gral, null, 400);
		}
	}
}	
		
.divModalContainer:target {
	opacity:1;
	pointer-events: auto;

    &__divModalDialog__a:hover { 
		background: #00d9ff; 
	}		
}		